.navbar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #2c4361;
  background-color: #f2ffff;
  padding: 16px 16px;
}
.navbar-logo {
  font-family: "Major Mono Display", monospace;
  font-size: 18px;
  font-weight: bold;
}
.navbar-links {
  /* display: inline-block; */
  font-size: 14px;
  background-color: #f2ffff;
}

.navbar a {
  color: #2c4361;
  text-decoration: none;
  padding: 10px;
  border-radius: 8px;
}

