.home-hero-banner {
  flex: 1 1 100%;
  position: relative;
  margin: 64px 0 0 0;
  padding-bottom: 40px;
  /* padding: 160 48px 160px 48px; */
  background: #f2ffff;
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(44, 67, 97, 0.5)),
      to(rgba(44, 67, 97, 0.5))
    ),
    url(/static/media/welcome-banner.c0e21d44.jpg);
  background-image: linear-gradient(
      rgba(44, 67, 97, 0.5),
      rgba(44, 67, 97, 0.5)
    ),
    url(/static/media/welcome-banner.c0e21d44.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 100% 50%;
}

.hero-home {
  width: 100%;
  overflow: hidden;
  height: auto;
}
.intro {
  font-size: 2rem;
  padding-top: 10px;
  color: whitesmoke;
  font-family: "Muli", sans-serif;
}
.intro-text {
  color: whitesmoke;
  font-family: "Muli", sans-serif;
  padding: 20px;
  margin: 1.2em 0 1.2em 0;
}
.left-header,
.right-header {
  padding: 20px;
  font-family: "Muli", sans-serif;
  font-size: 1.75rem;
  margin: 0 0 0.75rem 0;
}

.left-text-quote,
.right-text {
  padding: 10px;
  line-height: 150%;
  font-size: 1rem;
}
.left-text-author {
  padding: 10px;
  font-size: 0.8rem;
  font-weight: bold;
}
.right-text {
  padding-bottom: 40px;
}

/* Pattern styles */
.container {
  display: flex;
  flex-flow: wrap;
  text-align: center;
}

.left-half {
  background-color: #c6e8e9;
  flex: 1 1 100%;
  padding: 1rem;
}
.right-half {
  background-color: #c6e8e9;
  padding: 1rem;
}
.image-cropper {
  display: inline-block;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.profile-photo {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 50%;
  overflow: hidden;
}
.contact-me-btn {
  font-family: "Muli", sans-serif;
  font-size: 16px;
  display: inline-block;
  background: #c6e8e9;
  color: #2c4361;
  padding: 0.5em 1.5em 0.5em 1.5em;
  border-radius: 50px;
  border: none;
}
.contact-me-btn:hover {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2c4361;
  padding: 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.project-header-container {
  flex: 1 1 100%;
  font-family: "Muli", sans-serif;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

@media only screen and (min-width: 500px) {
  .left-half {
    background-color: #c6e8e9;
    flex: 1 1;
    padding: 1rem;
  }
  .right-half {
    background-color: #2c4361;
    color: white;
    flex: 1 1;
    padding: 1rem;
  }

  .home-hero-banner {
    background-size: cover;
    padding-bottom: 40px;
  }
}

@media only screen and (min-width: 750px) {
  .intro {
    font-size: 4rem;
    padding-top: 50px;
  }
  .left-half,
  .right-half {
    flex: 1 1 100%;
  }
  h1 {
    font-size: 24px;
    font-weight: 900;
    font-style: italic;
  }
  h2 {
    font-size: 48px;
    font-weight: 500;
    line-height: 1.25;
  }
  h2 {
    font-size: 32px;
    font-weight: 500;
    line-height: 1.25;
  }
  .home-hero-banner {
    background-size: cover;
    padding-bottom: 120px;
  }
  .section-static {
    flex: 1 1 100%;
  }
  .contact-me-btn {
    font-weight: bold;
    font-size: 22px;
  }
}

.wrapper {
  height: 100vh;
  flex: 1 1 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 2px;
          perspective: 2px;
}

.section {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 0 0 5px #000;
}

.parallax::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transform: translateZ(-1px) scale(1.5);
          transform: translateZ(-1px) scale(1.5);
  background-size: 100%;
  z-index: -1;
}

/* The styling for the static div. */
.static {
  background: #2c4361;
  padding: 42px;
  height: 50vh;
}
.static-link {
  background: #2c4361;
  padding: 42px;
  height: 20vh;
}
.macbook-img {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 380px;
  height: auto;
  padding: 20px;
}
.vidly-mobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 380px;
  height: auto;
  border: none;
}
.lashgirl-ecomm {
  width: 320px;
  font-size: 0px;
}
.lashgirl-ecomm2 {
  width: 320px;
}

.text-overlay {
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(44, 67, 97, 0.2)),
    to(rgba(44, 67, 97, 0.2))
  );
  background-image: linear-gradient(
    rgba(44, 67, 97, 0.2),
    rgba(44, 67, 97, 0.2)
  );
}

.bg1::after {
  background-image: url("https://images.unsplash.com/photo-1548777123-e216912df7d8?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2475&q=80");
  background-repeat: no-repeat;
}

.bg2::after {
  background-image: url(/static/media/vidly.0479efaa.png);
  background-repeat: no-repeat;
}
.bg3::after {
  background-image: url(/static/media/vidly-peek.10ad5e84.png);
  background-repeat: no-repeat;
}
.bg:hover {
  opacity: 0.3;
}
.bg4::after {
  background-image: url("https://images.unsplash.com/photo-1570475735025-6cd1cd5c779d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2250&q=80");
  background: cover;
  background-repeat: no-repeat;
}

@media only screen and (min-width: 750px) {
  .section {
    width: 60vw;
  }
  .static {
    height: auto;
    width: 100%;
  }
  .macbook-img {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    width: 600px;
    height: auto;
  }
  .parallax::after {
    top: 0;
    right: 8;
    bottom: 0;
    left: 3;
    -webkit-transform: translateZ(-1px) scale(2.4);
            transform: translateZ(-1px) scale(2.4);
  }
}

.cs-title {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 10px;
    /* background-color: #A9C6C7 */
}
.case-study {
    background-color: #A9C6C7;
